<script setup>
document.title = '评论';
import {NBadge, NButton, NEllipsis, NIcon, NSwitch, NTag, useLoadingBar, useMessage} from "naive-ui";
import {h, reactive} from "vue";
import {
  CommentOutlined,
  FolderOutlined,
  HomeOutlined,
  InboxOutlined,
  SettingOutlined,
  TagOutlined,
  UserOutlined
} from "@vicons/antd";
import {useRouter} from "vue-router";
import {adminChangeStatus, adminGetComment} from "@/assets/js/api";

const router = useRouter()
const loadingBar = useLoadingBar()
const message = useMessage()
const renderIcon = (icon) => {
  return () => h(NIcon, null, {default: () => h(icon)});
}

const data = reactive({
  commentData: [],
  loading: false,
})

const queryData = reactive({
  kwd: '',
  order: 'asc',
  orderby: 'id',
})

const orderOption = [
  {
    label: 'ID',
    value: 'id'
  },
  {
    label: '用户',
    value: 'nickname'
  },
  {
    label: '状态',
    value: 'is_check'
  },
  {
    label: '创建时间',
    value: 'create_time'
  },
  {
    label: '更新时间',
    value: 'update_time'
  }]
const menuOptions = [
  {
    label: '首页',
    key: 'adminHome',
    icon: renderIcon(HomeOutlined),
  },
  {
    label: '资源',
    key: 'adminResource',
    icon: renderIcon(InboxOutlined),
  },
  {
    label: '分类',
    key: 'adminCategory',
    icon: renderIcon(FolderOutlined),
  },
  {
    label: '标签',
    key: 'adminTag',
    icon: renderIcon(TagOutlined),
  },
  {
    label: '评论',
    key: 'adminComment',
    icon: renderIcon(CommentOutlined),
  },
  {
    label: '用户',
    key: 'adminUser',
    icon: renderIcon(UserOutlined),
  },
  {
    label: '系统',
    key: 'adminSystem',
    icon: renderIcon(SettingOutlined),
  }
]

const handleSelect = (key) => {
  router.push({name: key})
}

const columns = reactive([
  {
    title: 'ID',
    key: 'id',
    align: 'center'
  },
  {
    title: '用户',
    key: 'nickname',
    align: 'center',
    render(row) {
      return h('div', [
        h('span', row.user.nickname),
      ])
    }
  },
  {
    title: '内容',
    key: 'content',
    align: 'center',
    render(row) {
      return h('div', [
        h(NEllipsis, {tooltip: true, lineClamp: 1}, row.content),
      ])
    }
  },
  {
    title: '回复对象',
    key: 'reply',
    align: 'center',
    render(row) {
      let children = []
      if (row.reply) {
        if (row.reply.is_check) {
          children = [
            h(NBadge, {
                  color: '#18a058',
                  dot: true,
                  style: {
                    padding: '0 5px'
                  }
                }, [
                  h(NTag, {
                    type: 'info',
                    round: true,
                    size: 'small',
                    style: {
                      marginRight: '5px'
                    }
                  }, row.reply.id),
                  h(NEllipsis, {tooltip: true, lineClamp: 1}, row.reply.content),
                ]
            )
          ]
        } else {
          children = [
            h(NBadge, {
                  color: '#2478e5',
                  dot: true,
                  style: {
                    padding: '0 5px'
                  }
                }, [
                  h(NTag, {
                    type: 'info',
                    round: true,
                    size: 'small',
                    style: {
                      marginRight: '5px'
                    }
                  }, row.reply.id),
                  h(NEllipsis, {tooltip: true, lineClamp: 1}, row.reply.content),

                ]
            )
          ]
        }

      } else {
        children = h('span', row.reply.content)
      }
      return h('div', children)
    }
  },
  {
    title: '资源',
    key: 'resource',
    align: 'center',
    render(row) {
      return h('div', [
        h('span', row.resource.name),
      ])
    }
  },
  {
    title: '发布时间',
    key: 'create_time',
    align: 'center',
  },
  {
    title: '审核通过',
    key: 'is_check',
    align: 'center',
    render(row) {
      return h(NSwitch, {
        value: row.is_check,
        onUpdateValue: (value) => {
          const params = {
            type: 'comment',
            id: row.id,
            status: value
          }
          adminChangeStatus(params).then(res => {
            if (res.data.code === 200) {
              message.success(res.data.msg)
              getComment()
            } else if (res.data.code === 400) {
              message.error(res.data.msg)
            } else if (res.data.code === 401) {
              message.warning(res.data.msg)
            }
          }).catch(err => {
            message.error(err.message)
          })
        }
      })
    }
  },
  {
    title: '操作',
    key: 'action',
    width: 200,
    align: 'center',
    render(row) {
      return h('div', [
        h(NButton, {
          type: 'primary',
          size: 'small',
          secondary: true,
          style: {
            marginRight: '10px'
          },
          onClick: () => {
            router.push({
              name: 'resource',
              query: {
                resourceId: row.resource.id
              }
            })
          }
        }, {default: () => '定位资源'}),
      ])
    }
  }
])

const getComment = () => {
  loadingBar.start()
  data.loading = true
  adminGetComment(queryData).then(res => {
    if (res.data.code === 200) {
      data.commentData = res.data.data
      loadingBar.finish()
      data.loading = false
    } else if (res.data.code === 400) {
      message.error(res.data.msg)
      data.loading = false
      loadingBar.error()
    } else if (res.data.code === 401) {
      message.warning(res.data.msg)
      loadingBar.error()
      data.loading = false
    }
  }).catch(err => {
    message.error(err.message)
    loadingBar.error()
  })
}
getComment()
</script>

<template>
  <n-layout>
    <n-layout-header style="padding: 24px">
      <n-page-header @back="router.push({name: 'adminHome'})">
        <template #header>
          <n-breadcrumb>
            <n-breadcrumb-item>
              <n-dropdown :options="menuOptions" @select="handleSelect">
                <div>
                  <n-icon :component="HomeOutlined" size="16"/>
                  首页
                </div>
              </n-dropdown>
            </n-breadcrumb-item>
            <n-breadcrumb-item>
              <n-icon :component="CommentOutlined" size="16"/>
              评论
            </n-breadcrumb-item>
          </n-breadcrumb>
        </template>
        <template #title>
          评论
        </template>
        <template #default>
          <n-grid :cols="4" :x-gap="24" :y-gap="24">
            <n-grid-item :span="1">
              <n-input v-model:value="queryData.kwd" placeholder="请输入用户昵称" @update:value="getComment"></n-input>
            </n-grid-item>
            <n-grid-item :span="1" style="display: flex">
              <n-radio-group v-model:value="queryData.order" style="margin-right: 10px" @update:value="getComment">
                <n-radio-button value="asc">升序</n-radio-button>
                <n-radio-button value="desc">降序</n-radio-button>
              </n-radio-group>
              <n-select v-model:value="queryData.orderby" :options="orderOption" style="width: 100px"
                        @update:value="getComment"/>
            </n-grid-item>
          </n-grid>
        </template>
      </n-page-header>
    </n-layout-header>
    <n-layout-content style="padding: 24px 0">
      <n-data-table :columns="columns" :data="data.commentData" :loading="data.loading" :max-height="450"/>
    </n-layout-content>
  </n-layout>
</template>

<style scoped>

</style>